import Vue from 'vue'
import VueRouter from 'vue-router'
import routes from './routes'
import store from '@/store'
import { Notify } from 'vant';

Vue.use(VueRouter);

const router = new VueRouter({
    routes,
    base: process.env.BASE_URL,
})

router.beforeEach((to, from, next)=> {
    const state = store.state.loginUser.user;
    if(!to.meta.auth) {
        if(!state) {
            Notify({ type: 'danger', message: '该页面需要登录，请先登录',
            duration: 1200, });
            next({
                name: "login", 
            })
        } else {
            next();
        }
    } else {
        if(state && to.name === "login") {
            next({name: "home"});
        }else {
            next();
        }
    }
})


export default router;