export default{
    namespaced: true,
    state: {
        list: [],
    },
    mutations: {
        addCar(state, payload) {
            if(state.list.length === 0) {
                state.list.push({
                    ...payload,
                    num: 0,
                });
            }
            state.list.forEach((item, index)=> {
                if(item.id !== payload.id) {
                    state.list.push({
                        ...payload,
                        num: 1,
                    });
                }
            })
        },
        del({list}, id) {
            list.forEach((item, index)=> {
                if(item.id === id) {
                    list.splice(index, 1);
                }
            }) 
        },
    }
}