import "nprogress/nprogress.css";
import {start, done, configure} from 'nprogress'
configure({
    trickleSpeed: 20,
    showSpinner: false,
});
function delay(duration) {
    return new Promise(resolve => {
        setTimeout(()=> {
            resolve();
        }, duration);
    })
}

function getPageComponent(pageCompResolver) {
    return async () =>{
        start();
        await delay(1200);
        const comp = await pageCompResolver();
        done();
        return comp;
    }
}

