export default{
    namespaced: true,
    state: {
        list: [],
    },
    getters: {
        total({list}) {
            const result =list.reduce((per, item, index)=> {
                return per + item.num *item.pri;
            }, 0);
            return result.toFixed(2);
        }, 
        totalNum({list}) {
            return list.reduce((per, item, index)=> {
                return per + item.num;
            }, 0);
        }
    }, 
    mutations: {
        addCar(state, payload) {
                /* 
                id: （…）
                name: （…）
                pri: （…）
                src: （…）
                
                */
            if(state.list.length === 0) {
                state.list.push({
                    ...payload,
                    num: 0,
                });
            }
            state.list.forEach((item, index)=> {
                if(item.id === payload.id) {
                    item.num ++;
                } else {
                    state.list.push({
                        ...payload,
                        num: 1,
                    });
                }
            })
        },
        sub({list}, id) {
            list.forEach((item, index)=> {
                if(item.id === id) {
                    item.num -= 1;
                    if(item.num < 1) {
                        item.num = 1;
                    }
                }
            }) 
        },
        add({list}, id) {
            list.forEach((item, index)=> {
                if(item.id === id) {
                    item.num += 1;
                }
            }) 
        },
        del({list}, id) {
            list.forEach((item, index)=> {
                if(item.id === id) {
                    list.splice(index, 1);
                }
            }) 
        },
        clear(state) {
                state.list = [];
        }
    }
}